.mainContainerAbout {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 25px;
}

.textContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  height: 300px;
  width: 500px;
  box-sizing: border-box;
  padding: 1px 30px;
}

.textContainer h3 {
  margin: 0px;
  color: #f7f7f7;
  font-size: 25px;
  font-weight: 400;
}

.textContainer p {
  margin: 0px;
  color: #aaaaaa;
  font-weight: 200;
}

.imageAboutContainer img {
  max-width: 500px;
}

@media (max-width: 1050px) {
  .mainContainerAbout {
    flex-direction: column;
  }

  .imageAboutContainer img {
    max-width: 500px;
  }

  .textContainer {
    padding: 1px 15px;
  }
}

@media (max-width: 540px) {
  .imageAboutContainer img {
    max-width: 400px;
  }

  .textContainer {
    width: 400px;
    padding: 1px 5px;
  }
}

@media (max-width: 450px) {
  .imageAboutContainer img {
    max-width: 350px;
  }

  .textContainer {
    width: 350px;
  }
}
